* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer-container {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.timer {
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  font-size: 24px;
  display: flex;
}

.value {
  min-width: 40px;
  text-align: center;
  background-color: #f7f7f7;
  border-radius: 5px;
  padding: 5px 10px;
  font-weight: bold;
}

.label {
  margin-top: 5px;
}

